import React from "react";
import Bird from "../images/bird.jpg";
import Tfas from "../images/tfas.jpg";
import TimeMachine from "../images/time-machine.jpg";

const BookBody = () => (
	<div className="body">
		<div
			className="body-box box-1"
			style={{
				display: "grid",
				justifyContent: "center"
			}}
		>
			<img src={Bird} alt="book" className="book-img" />
			<div className="book-info mt-4">
				<p className="mb-0">Bird by Bird</p>
				<p className="muted">Anne Lamott</p>
			</div>
		</div>
		<div
			className="body-box box-2"
			style={{
				display: "grid",
				justifyContent: "center"
			}}
		>
			<img src={Tfas} alt="book" className="book-img" />
			<div className="book-info mt-4">
				<p className="mb-0">Thinking fast and slow</p>
				<p className="muted">Daniel Kahneman</p>
			</div>
		</div>
		<div
			className="body-box box-3"
			style={{
				display: "grid",
				justifyContent: "center"
			}}
		>
			<img src={TimeMachine} alt="book" className="book-img" />
			<div className="book-info mt-4">
				<p className="mb-0">Your brain is a time machine</p>
				<p className="muted">Dean Buonomano</p>
			</div>
		</div>
	</div>
);

export default BookBody;



